<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Add New Access Rights</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container">
                <div class="row q-col-gutter-md">
                    <div class="col-md-9 col-12 comp-grid">
                        <q-card   class="q-pa-md nice-shadow-18">
                            <div>
                                <ValidationObserver ref="observer" v-slot="{ invalid }" tag="form" @submit.prevent="startSaveRecord()" @reset="resetForm">
                                    <div class="row q-col-gutter-md">
                                        <div class="col-12">
                                            <div class="row">
                                                <div class="col-sm-3 col-12">
                                                    Access List Id *
                                                </div>
                                                <div class="col-sm-9 col-12">
                                                    <ValidationProvider :rules="{required:true}" name="Access List Id" v-slot="{ errors, invalid, validated }">
                                                        <q-input outlined dense  ref="ctrlaccess_list_id" v-model="formData.access_list_id"  label="Access List Id" type="number" placeholder="Enter Access List Id"   step="any" list="access_list_id_list"  
                                                        class="" :error="invalid && validated" :error-message="errors[0]">
                                                        </q-input>
                                                        <datalist id="access_list_id_list">
                                                        <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                        </datalist>
                                                    </ValidationProvider>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-12">
                                            <div class="row">
                                                <div class="col-sm-3 col-12">
                                                    Set By *
                                                </div>
                                                <div class="col-sm-9 col-12">
                                                    <ValidationProvider :rules="{required:true}" name="Set By" v-slot="{ errors, invalid, validated }">
                                                        <q-input outlined dense  ref="ctrlset_by" v-model="formData.set_by"  label="Set By" type="number" placeholder="Enter Set By"   step="any" list="set_by_list"  
                                                        class="" :error="invalid && validated" :error-message="errors[0]">
                                                        </q-input>
                                                        <datalist id="set_by_list">
                                                        <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                        </datalist>
                                                    </ValidationProvider>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-12">
                                            <div class="row">
                                                <div class="col-sm-3 col-12">
                                                    User Id *
                                                </div>
                                                <div class="col-sm-9 col-12">
                                                    <api-data-source @loaded="response => mapOptionField(response, 'user_id')"  api-path="components_data/user_id_option_list"  v-slot="req">
                                                        <ValidationProvider :rules="{required:true}" name="User Id" v-slot="{ errors, invalid, validated }">
                                                            <q-select   :loading="req.loading"  outlined dense  ref="ctrluser_id" emit-value map-options  v-model="formData.user_id" :options="req.response" label="User Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                            <template v-slot:option="scope">
                                                                <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                    <q-item-section v-if="scope.opt.image" avatar>
                                                                        <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                    </q-item-section>
                                                                    <q-item-section>
                                                                        <q-item-label />{{ scope.opt.label }}
                                                                            <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                        </q-item-section>
                                                                    </q-item>
                                                                </template>
                                                                <template v-slot:selected-item="scope">
                                                                    <q-item  v-if="scope.opt">
                                                                        <q-item-section v-if="scope.opt.image" avatar>
                                                                            <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                        </q-item-section>
                                                                        <q-item-section>
                                                                            <q-item-label />{{ scope.opt.label }}
                                                                                <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                            </q-item-section>
                                                                        </q-item>
                                                                    </template>
                                                                    </q-select> 
                                                                </ValidationProvider>
                                                            </api-data-source>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div v-if="showSubmitButton" class="text-center q-my-md">
                                                <q-btn type="submit"    :rounded="false"  color="primary"  no-caps  unelevated   :disabled="invalid" icon="send" :loading="saving">
                                                    <q-spinner-oval slot="loading" /> Submit
                                                </q-btn>
                                            </div>
                                        </ValidationObserver>
                                        <slot :submit="submit" :saving="saving"></slot>
                                    </div>
                                </q-card>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </template>
        <script>
	import { PageMixin } from "../../mixins/page.js";
	import { AddPageMixin } from "../../mixins/addpage.js";
	import { mapActions } from "vuex";
	export default {
		name: 'addAccessrightsPage',
		components: {
		},
		mixins: [PageMixin, AddPageMixin ],
		props:{
			pageName : {
				type : String,
				default : 'access_rights',
			},
			routeName : {
				type : String,
				default : 'access_rightsadd',
			},
			apiPath : {
				type : String,
				default : 'access_rights/add',
			},
		},
		data() {
            return {
				formData: {
					access_list_id: "", set_by: "", user_id: "", 
				},
			}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Add New Access Rights"
				}
			}
		},
		meta () {
			return {
				title: this.pageTitle
			}
		},
		methods: {
			...mapActions('access_rights', ['saveRecord']),
			async startSaveRecord(){
				const isFormValid = await this.$refs.observer.validate();
				if(isFormValid){
					this.saving = true;
					let payload = this.normalizedFormData();
					let url = this.apiUrl;
					let data = { url, payload  }
					this.saveRecord(data).then((response) => {
						this.record = response.data;
						this.id = this.record['id'];
						this.saving = false;
						this.resetForm();
						this.closeDialogs();// close page dialog that if opened
						this.flashMsg(this.msgAfterSave);
						this.navigateTo(`/access_rights`);
					},
					 (response) => {
						this.saving = false;
						this.showPageRequestError(response);
					});
				}
			},
			resetForm (){
				this.formData = {access_list_id: "", set_by: "", user_id: "", };
				requestAnimationFrame(() => {
					this.$refs.observer.reset();
				});
				this.$EventBus.$emit("resetForm");
			},
		},
		mounted() {
		},
	};
</script>
<style scoped>
</style>
